<template>
  <ul
    v-if="payments.length"
    class="payment-icons"
    :class="modifiers"
  >
    <li
      v-for="(payment) in payments"
      :key="`payment-${payment.type}`"
      class="payment-icons__item"
      :arial-label="payment.type"
      v-html="payment.icon"
    />
  </ul>
</template>

<script>

/**
 * Vue: Payment icons (payment-icons)
 * -----------------------------------------------------------------------------
 * Displays payment icons for enabled payment gateway.
 *
 * @param {String} [modifiers] - Block level class to apply to all elements.
 * @param {Array} payments - Array of payment options.
 *
 */
export default {
  name: 'PaymentIcons',

  props: {
    modifiers: {
      type: String,
      default: '',
    },
    payments: {
      type: Array,
      default: () => ([]),
      required: true,
    },
  },
}
</script>

<style lang="scss">
@import './payment-icons';
