<script>

/**
 * Vue: Cart summary (main-cart)
 * -----------------------------------------------------------------------------
 * Summary of cart metrics with checkout CTA.
 *
 */
import { mapState } from 'vuex'

import Btn from '~global/btn/btn'
import PaymentIcons from '~async/payment-icons/payment-icons'

export default {
  name: 'CartSummary',

