    Btn,
    PaymentIcons,
  },

  props: {
    payments: {
      type: Array,
      default: () => ([]),
    },
    partnerFeature: {
      type: [Object, Boolean],
      default: false,
    },
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      cart: (state) => state.cart.response,
    }),
  },
}
</script>

